export default {
	data() {
	return {
			PageCur: 'Home',
			cuIconList: [{
			    cuIcon: 'ecdfdd',
				badge: 0,
				name: '时事新闻',
				url: '/pages/index/index_longjing/Current_news',
				
			}, {
				cuIcon: 'ecff', 
				badge: 0,
				name: '党建联盟',
				url: '/pages/index/index_longjing/index_Alliance',
				
			}, {
				 cuIcon: 'ecb',
				badge: 0,
				name: '工作动态',
				url: '/pages/index/index_longjing/Work_dynamics',
				
			}, {
			    cuIcon: 'eccbfa', 
				badge: 0,
				name: '社区概况',
				url: '/pages/index/index_longjing/Com_overview',
				
			}, {
				cuIcon: 'edbd', 
				badge: 0,
				name: '社区风采',
				url: '/pages/index/index_longjing/Community style',
			
			}],
			modalName: null,
			gridCol: 3,
			gridBorder: false,
			menuBorder: false,
			menuArrow: false,
			menuCard: false,
			skin: false,
			listTouchStart: 0,
			listTouchDirection: null,
		}
	},
	methods: {
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		Gridchange(e) {
			this.gridCol = e.detail.value
		},
		Gridswitch(e) {
			this.gridBorder = e.detail.value
		},
		MenuBorder(e) {
			this.menuBorder = e.detail.value
		},
		MenuArrow(e) {
			this.menuArrow = e.detail.value
		},
		MenuCard(e) {
			this.menuCard = e.detail.value
		},
		SwitchSex(e) {
			this.skin = e.detail.value
		},
			
		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},
			
		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},
			
		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		},
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
		
	}	
}
